.popup__card-delete {
    display: flex;
    flex-direction: column;
    width: 430px;
    background-color: #fff;
    position: fixed;
    z-index: 3;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    transition: visibility .8s, opacity .8s, ease-in-out .8s;
  }

  @media screen and (max-width: 700px) and (min-width: 320px) {
    .popup__card-delete {
      width: 282px;
    }
  }


