.popup__submit {
  width: 358px;
  height: 50px;
  margin: 48px auto 37px;
  border: none;
  background-color: black;
  font-family: 'Inter', 'Times New Roman', Times, serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #FFFFFF;
}

.popup__submit:hover {
  opacity: .8;
  cursor: pointer;
}

@media screen and (max-width: 700px) and (min-width: 320px) {
  .popup__submit {
    width: 238px;
    height: 46px;
    margin: 40px auto 25px;
    font-size: 14px;
    line-height: 17px;
  }
}
