.header {
  max-width: 880px;
  margin: auto;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  padding-bottom: 41px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media screen and (max-width: 700px) and (min-width: 320px) {
  .header {
    max-width: 100%;
    padding-bottom: 31px;
    margin: 0;
  }
}
